export default {
	data() {
		return {
			title:"是否确认核销？",
			isShow: false,
			text: "",
			inputValue: ""
		}
	},
	methods: {
		fail(res) {
			this.isShow=false
			uni.showToast({
				title: "已取消",
				icon: 'none',
				duration: 2000
			})
			return
		},
		succer(res) {
			var _this=this
			_this.isShow=false
			uni.showLoading({
				title:'销毁中...'
			})
			var data = {
				invite_code: _this.inputValue
			}
			_this.$http.getInvitation(data).then(res => {
				uni.hideLoading()
				console.log(res);
				let resData = res.data;
				uni.showToast({
					title: resData.msg,
					icon: 'none',
					duration: 2000
				})
				_this.inputValue = ''
			})
		},
		deliveryInput(e) {
			this.inputValue = e.detail.value
		},
		submit() {
			var _this = this;
			if(!_this.inputValue) return 
			this.isShow=true
			this.text='邀请码：' + _this.inputValue
			
			return
			uni.showModal({
				title: '是否确定核销？',
				content: '邀请码：' + _this.inputValue,
				confirmText: '确定',
				confirmColor: '#eb3c39',
				cancelText: '取消',
				cancelColor: '#030303',
				success: function(res) {
					if (res.confirm) {
					
					} else {

					}
				}
			})
		},
		scanCode() {
			var _this = this;
			// 只允许通过相机扫码
			uni.scanCode({
				onlyFromCamera: true,
				success: function(res) {
					_this.inputValue = res.result;
					_this.submit();
				}
			});
		}
	}
}
